
import { defineComponent, PropType } from 'vue';
import { GameMode } from '../types/types';

export default defineComponent({
  props: {
    modes: {
      type: Array as PropType<GameMode[]>,
      required: true,
    },
    currentMode: {
      type: String as PropType<GameMode>,
      required: true,
    },
    isSound: {
      type: Boolean,
    },
  },
  data() {
    return {
      showRules: false,
    };
  },
  computed: {
    soundIconName(): string {
      if (this.isSound) {
        return 'volume_up';
      }
      return 'volume_off';
    },
  },
});
