import { defineComponent, PropType } from 'vue';
import { Weapon } from '../types/types';

export default defineComponent({
  props: {
    weaponName: String as PropType<Weapon>,
  },
  methods: {
    getImgPath() {
      return require(`../assets/images/${this.weaponName}.png`);
    },
  },
});
